import React from "react";
import ReactDOM from "react-dom/client";
import App from "./App";

export function initializeApp(container) {
  if (!container) {
    console.error("Le conteneur pass  initializeApp est invalide.");
    return;
  }

  console.log("Container reu par initializeApp :", container);

  // Ajout d'un paramtre `ownerDocument` pour prendre en compte le ShadowRoot
  const root = ReactDOM.createRoot(container, {
    hydrate: false,
    // Forcer React  comprendre que le `document` du Shadow DOM doit tre utilis
    document: container.ownerDocument,
  });

  root.render(
    <React.StrictMode>
      <App />
    </React.StrictMode>
  );
}

// Attacher la fonction globale  `window`
window.initializeApp = initializeApp;